.privacy-policy {
  display: flex;
  flex-direction: column;
}

.privacy-policy__button-wrapper {
  @include viewport--sm {
    margin-top: -13px;
  }
}

.privacy-policy__date {
  display: block;

  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  color: $color-gray;

  @include viewport--sm {
    letter-spacing: 0.01em;
  }
}

.privacy-policy__wrapper {
  max-width: 936px;
  margin: 10px auto 0 auto;

  @include viewport--sm {
    margin-top: 15px;
  }
}

.privacy-policy__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.privacy-policy__list li {
  margin-bottom: 24px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.privacy-policy__list h3 {
  margin-top: 11px;
  margin-bottom: -3px;

  font-size: 24px;
  font-weight: lighter;
  line-height: 29px;
  letter-spacing: 0.043em;
  color: $color-blue;

  @include viewport--md {
    margin-bottom: 10px;
  }
}
